import './App.css';
import React, { useState, useCallback, useEffect } from "react";
import { ethers } from "ethers";
import { MetaMaskInpageProvider } from '@metamask/providers';

import TheMetaMoais from "./artifacts/TheMetaMoais.json";
import ERC20Utils from './util/ERC20Utils';
import ERC721Utils from './util/ERC721Utils';
import WalletUtils from './util/WalletUtils';
import { useWallet } from './util/useWallet';

import c from "./constants";
const constants = c["MAIN"];
const correctChainId = constants.correctChainId;
const correctChainUrl = constants.correctChainUrl;
const addrMetamoiasContract = constants.addrMetamoiasContract;
const urlMetamoiasEtherscan = constants.urlMetamoiasEtherscan;
const MAX_NUM_TO_MINT = constants.MAX_NUM_TO_MINT;
const FREE_MINT_THRESHOLD = constants.FREE_MINT_THRESHOLD;

declare global {
    interface Window {
        ethereum: MetaMaskInpageProvider;
    }
}


function App() {
  const wallet = useWallet({
    onWalletAccountChanged: (address: string) => { if (!!address) { loadWalletData(address); } },
    onWalletConnected: (address: string) => { if (!!address) { loadWalletData(address); } }
  });

  // State...
  const [isLoading, setIsLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [isOnCorrectChain, setIsOnCorrectChain] = useState(false);
  const [mintCost, setMintCost] = useState<string>("");
  const [numToMint, setNumToMint] = useState<number>(0);
  const [numAlreadyMinted, setNumAlreadyMinted] = useState<number>(0);

  // Set document title...
  useEffect(() => { document.title = "InvsblePunks"; })

  // Listen to wallet/network changes...
  useEffect(() => { addNetworkListener(); }, []);

  // Loads all necessary data associated with the currently connected wallet...
  const loadWalletData = useCallback(async (address) => {
    setIsLoading(true);

    try {
        // Load the cost of minting...
        const cost = await getMintCost(addrMetamoiasContract);
        if (cost !== null) { setMintCost(cost.toString()); }

        // Load total number already minted (not wallet data but w/e)...
        const totalSupply = await ERC721Utils.getTotalSupply(addrMetamoiasContract);
        if (!!totalSupply) { setNumAlreadyMinted(totalSupply); }
    } catch(err) {
        setIsLoading(false);
    }

    setIsLoading(false);
  }, [setIsLoading, setNumAlreadyMinted, setMintCost]);

  // Try to connect wallet on load...
  useEffect(() => {
    // Async because useEffect must be synchronous...
    async function checkNetwork() {
        // Check correct chain...
        if (window.ethereum) {
          const chainId = await window.ethereum.request({ method: 'eth_chainId' }) as string;
          setIsOnCorrectChain(chainId === correctChainId);
        }
    }    

    // Check for wallet on load...
    checkNetwork();
  }, [loadWalletData, setIsOnCorrectChain]);

  // "Connect Wallet" button...
  const onConnectWalletClick = useCallback(async () => {
    await wallet.connect();
    if (wallet.isConnected) { loadWalletData(wallet.address); }
  }, [loadWalletData]);

  // "Disconnect Wallet" button...
  const onDisconnectWalletClick = useCallback(()=> {
    wallet.disconnect();
    loadWalletData("");
  }, [loadWalletData]);

  // "Switch Network" button...
  const onSwitchNetworkClick = useCallback(() => {
    WalletUtils.switchToNetwork(parseInt(correctChainId), correctChainUrl);
  }, []);

  // "-" button...
  const onMinusClick = useCallback(() => {
    if (numToMint > 0) { setNumToMint(numToMint - 1); }
  }, [numToMint, setNumToMint]);

  // "+" button...
  const onPlusClick = useCallback(() => {
    if (numToMint < MAX_NUM_TO_MINT) { setNumToMint(numToMint + 1); }
  }, [numToMint, setNumToMint]);

  // "Mint" button...
  const onMintClick = useCallback(async () => {
    setErrorMessage("");
    const result = await mint(addrMetamoiasContract, numToMint, numAlreadyMinted);
    if (!result.success) { setErrorMessage(result.message); }
  }, [numToMint, numAlreadyMinted, setErrorMessage]);

  // Occurs when a chain/network is changed...
  function addNetworkListener() {
    if (!window.ethereum) { return; }
    window.ethereum.on("chainChanged", (accounts) => {
        setIsOnCorrectChain((window.ethereum && window.ethereum.chainId === correctChainId));
        loadWalletData(wallet.address);
    });
  }

  return (
    <div>
        <Navbar />
        <div id="root" className="self-start">
            <main className="">

                

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <LeftSideContent />

                    <RightSideContent isLoading={isLoading} 
                        isOnCorrectChain={isOnCorrectChain}
                        isWalletConnected={wallet.isConnected}
                        walletAddress={wallet.address}
                        mintCost={mintCost}
                        numToMint={numToMint}
                        numAlreadyMinted={numAlreadyMinted}
                        errorMessage={errorMessage}
                        onDisconnectWalletClick={onDisconnectWalletClick}
                        onConnectWalletClick={onConnectWalletClick}
                        onSwitchNetworkClick={onSwitchNetworkClick}
                        onMinusClick={onMinusClick}
                        onPlusClick={onPlusClick}
                        onMintClick={onMintClick}
                    />
                </div>
            </main>
        </div>
    </div>
  );
}

function Spacer() { return <div style={{ paddingTop: "10px" }}></div>; }

function ThemeButton(props: any) {
    return <button {...props} className="rounded rounded-md p-2 text-xl text-black bg-white disabled:opacity-50" onClick={props.onClick}>{props.children}</button>
}

// Black box with white text. Can place anything you want inside
function ContentBox(props: any) {
    return <div {...props} className="rounded-lg bg-black opacity-70 p-10 text-white">{props.children}</div>;
}

// Red box with black text. Can place anything you want inside
function ErrorBox(props: any) {
    return <div className="flex justify-center bg-red-400 text-black p-2">{props.children}</div>
}

// Formatted account information
function AccountInformation(props: any) {
    return (
        <>
            <div className="text-ellipsis overflow-hidden">
                Account:<br />{props.walletAddress}
            </div>
        </>
    );
}

// Block that can show...
//   1) Loading message
//   2) Wrong network message
//   3) Connect Wallet button
//   4) Account information for connected wallet
function AccountBlock(props: any) {
    const { isLoading, isOnCorrectChain, isWalletConnected, walletAddress } = props;

    // If we are loading, return a loading message...
    if (isLoading) { 
        return <div className="flex justify-center">Loading...</div>;
    }

    // If not on the correct chain/network, return an error message...
    if (!isOnCorrectChain) {
        return (
            <ContentBox>
                <ErrorBox>Please connect to the correct network.</ErrorBox>
                <div style={{ paddingTop: "10px" }} />
                <div className="flex justify-center">
                    <ThemeButton onClick={props.onSwitchNetworkClick}>Switch Network</ThemeButton>
                </div>
            </ContentBox>
        );
    }

    // If wallet is not connected, ask them to connect their wallet...
    if (!isWalletConnected) {
        return (
            <ContentBox>
                <ErrorBox>Please connect your wallet.</ErrorBox>
                <div style={{ paddingTop: "10px" }} />
                <div className="flex justify-center">
                    <ThemeButton onClick={props.onConnectWalletClick}>Connect Wallet</ThemeButton>
                </div>
            </ContentBox>
        );
    }

    // Otherwise, return their account information...
    return (
        <ContentBox>
            <AccountInformation walletAddress={walletAddress} />
            <div className="flex justify-center pt-2">
                <ThemeButton onClick={props.onDisconnectWalletClick}>Disconnect Wallet</ThemeButton>
            </div>
        </ContentBox>
    );
}

