export default App;


async function mint(addrERC721: string, amount: number, numAlreadyMinted: number): Promise<{ success: boolean, message: string }> {
    // Check for MetaMask...
    if (!window.ethereum) { return { success: false, message: "Metamask not found" }; }

    // Get contract...
    const provider = new ethers.providers.Web3Provider(window.ethereum as any);
    const signer = provider.getSigner();
    const contract = new ethers.Contract(addrERC721, TheMetaMoais.abi, signer);

    // Calculate cost...
    const singleMintCost = await getMintCost(addrERC721);
    if (singleMintCost === null) { return { success: false, message: "Could not determine cost to mint" }; }

    const isInFreePeriod = (numAlreadyMinted <= FREE_MINT_THRESHOLD);
    const mintCost = (isInFreePeriod) ? 0 : ethers.utils.parseEther(singleMintCost).mul(amount);
    console.log("Calcualted amount to mint", amount, mintCost.toString());

    // Send transaction...    
    try {
        const txHash = await contract.mint(amount, { value: mintCost });
        return { success: true, message: "" };
    } catch (error: any) {
        console.log(JSON.stringify(error));
        return { success: false, message: error?.error?.message || "Error minting" };
    }
}

// Calls Contract.mint({amount})...
async function getMintCost(addrMetamoias: string): Promise<string | null> {
    if (typeof window.ethereum == "undefined") { return null; }

    // Get contract...
    const signer = (new ethers.providers.Web3Provider(window.ethereum as any)).getSigner();
    const contract = new ethers.Contract(addrMetamoias, TheMetaMoais.abi, signer);
    

    const result = await contract.cost();   // Cost in wei
    console.log(result);
    console.log(result.toString());
    return ethers.utils.formatUnits(result, 18);
    //return ethers.utils.parseUnits(result, 18);
};