import { ethers } from "ethers";
import { MetaMaskInpageProvider } from "@metamask/providers";
import { IERC721Enumerable as IERC721 } from "openzeppelin-abis";

declare global {
    interface Window {
        ethereum: MetaMaskInpageProvider;
    }
}

export default class ERC721Utils {

    // Gets the totalSupply() of the given ERC721...
    public static async getTotalSupply(addrERC721: string): Promise<number | null> {
        // Check for MetaMask...
        if (!window.ethereum) { return null; }

        // Get contract...
        const provider = new ethers.providers.Web3Provider(window.ethereum as any);
        const erc721 = new ethers.Contract(addrERC721, IERC721.abi, provider);
        try {
            const totalSupply = await erc721.totalSupply();
            return totalSupply.toNumber();
        } catch(err) { 
            return null;
        }
    }

    public static async isERC721ApprovedForAll(addrERC721: string, addrOwner: string, addrOperator: string): Promise<boolean> {
        // Check for MetaMask...
        if (!window.ethereum) { return false; }

        // Get contract...
        const provider = new ethers.providers.Web3Provider(window.ethereum as any);
        const erc721 = new ethers.Contract(addrERC721, IERC721.abi, provider);
        try {
            const isApproved = await erc721.isApprovedForAll(addrOwner, addrOperator);
            return isApproved;
        } catch(err) { 
            return false;
        }
    }

    // Calls setApprovalForAll() for {addrApproval} for the given ERC721 token...   
    public static async approveForAll(addrToken: string, addrApproval: string): Promise<boolean> {
        // Check for MetaMask...
        if (!window.ethereum) { return false; }

        // Get contract...
        const provider = new ethers.providers.Web3Provider(window.ethereum as any);
        const signer = provider.getSigner();
        const contract = new ethers.Contract(addrToken, IERC721.abi, signer);

        // Send transaction...
        try {
            const txHash = await contract.setApprovalForAll(addrApproval, true);
            return true;
        } catch (error) {
            return false;
        }
    }
}