import { useState, useEffect, useCallback } from 'react';
import WalletUtils from './WalletUtils';

export interface IUseWalletProps {
    onWalletAccountChanged?: (address: string) => void;
    onWalletConnected?: (address: string) => void;
    onWalletDisconnected?: () => void;
}

export function useWallet(props: IUseWalletProps) {
    const { onWalletAccountChanged, onWalletConnected, onWalletDisconnected } = props;
    const [isInitialized, setIsInitialized] = useState<boolean>(false);
    const [isConnected, setIsConnected] = useState<boolean>(false);
    const [address, setAddress] = useState<string>("");

    // Initialize wallet connection...
    useEffect(() => {
        if (!isInitialized) {

            setIsInitialized(true);
            WalletUtils.getCurrentWallet().then((address) => {
                // If we found an address...
                if (!!address) {
                    // Set state...
                    setAddress(address);
                    setIsConnected(true);

                    // Call client function...
                    if (onWalletConnected) { onWalletConnected(address ?? ""); }
                }
            });
        }
    }, [address, isInitialized, onWalletConnected]);

    // Listener for when a wallet account is changed...
    const _onWalletAccountChanged = useCallback((accounts: any) => {
        // Update state...
        const address = accounts.length > 0 ? accounts[0] : "";
        setAddress(address);
        setIsConnected(accounts.length > 0);

        // Call client function...
        if (onWalletAccountChanged) { onWalletAccountChanged(address); }
    }, [setIsConnected, setAddress, onWalletAccountChanged]);

    // Listen for wallet changes...
    useEffect(() => {
        if (!window.ethereum) { return; }

        // Listen to accountsChanged event...
        window.ethereum.on("accountsChanged", _onWalletAccountChanged);

        // Clean up listener when finished...
        return () => { window.ethereum.removeListener('accountsChanged', _onWalletAccountChanged); };
    }, [_onWalletAccountChanged]);

    // Disconnect wallet...
    const disconnect = useCallback(() => {
        // Disconnect wallet...
        setIsConnected(false);
        setAddress("");

        // Call client function...
        if (onWalletDisconnected) { onWalletDisconnected(); }
    }, [setIsConnected, setAddress, onWalletDisconnected]);

    // Connect wallet...
    const connect = useCallback(async () => {
        // Connect to wallet...
        const address = await WalletUtils.connectWallet();
        setIsConnected(address !== null);
        setAddress(address ?? "");

        // Call client function...
        if (onWalletConnected) { onWalletConnected(address ?? ""); }
    }, [setIsConnected, setAddress, onWalletConnected]);

    return { address, isConnected, connect, disconnect };
}